<template>
	<div id="commercial" class="warp">
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{path:'/shop/shopinfo'}">店铺信息</el-breadcrumb-item>
				<el-breadcrumb-item>编辑工商信息</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<section class="commercialinformation">
			<div class="title">
				编辑工商信息
			</div>
			<div class="info">
				<el-form label-width="155px">
					<el-form-item label="企业名称">
						<el-input v-model='forms.company_name' v-show='!Iautionoks'></el-input><span v-show='!Iautionoks' class="confms" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span>
						<span class="domain" v-show='Iautionoks'>{{shopsbusiness.company_name}} <span v-show='Iautionoks' class="confm" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span></span>
					</el-form-item>
					<el-form-item label="法人代表">
						<el-input v-model='forms.legal_person' v-show='!Iautionoks'></el-input><span v-show='!Iautionoks' class="confms" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span>
						<span class="domain" v-show='Iautionoks'>{{shopsbusiness.legal_person}} <span v-show='Iautionoks' class="confm" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span></span>
					</el-form-item>
					<el-form-item label="法人身份证号码">
						<el-input v-model='forms.legal_card' v-show='!Iautionoks'></el-input><span v-show='!Iautionoks' class="confms" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span>
						<span class="domain" v-show='Iautionoks'>{{shopsbusiness.legal_card}} <span v-show='Iautionoks' class="confm" :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span></span>
					</el-form-item>
					<el-form-item label="法人身份证照片" class='legar-card'>
						<div class="main clearfix">
							<div class="frontFace left">
							<h3 class=" titles">正面 <span class="confmd" v-show='show' :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span></h3>
							<div class="zm" v-if="legal_card_photo.length<1">
								<img src="../../img/larg_card.png" alt="" />
							</div>
							<div class="zm" v-else >
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+legal_card_photo[0]" alt="" />
								<big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
								<i class="el-icon-view" @click='clickImg'></i>
							</div>
								<img-upload @send-image='getLegarcard' :ImageSize='imgSize'></img-upload>
							</div>
							<div class="frontFace left">
							<h3 class=" titles">反面 <span class="confmd" v-show='show' :class="[{gr:Iautionoks},{reds:Iautionerrs},{or:Iinautions}]">({{idcert}})</span></h3>
							<div class="fm" v-if="legal_card_photo.length<2">
								<img src="../../img/larg_card2.png" alt="" />
							</div>
							<div class="fm" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+legal_card_photo[1]" alt="" />
								<big-img v-if="showimg" @clickit="viewimg" :imgSrc="Imgsrc"></big-img>
								<i class="el-icon-view" @click='clickimg'></i>
							</div>
								<img-upload @send-image='getLegarcard' :ImageSize='imgSize'></img-upload>
							</div>
							<div class="text-warp left">
								<p class="text">注：支持JPG、JPEG、PNG文件格式，每张大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="营业执照注册号">
						<el-input v-model='forms.business_num' v-show='!bautionerrs'></el-input><span @click="showipt" v-show='!bautionerrs' class="busNum" :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
						<span class="domain"  v-show='bautionerrs'>{{shopsbusiness.business_num}}</span> <span @click="showipt" v-show='bautionerrs'  :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
					</el-form-item>
					<el-form-item class='dates' label="营业期限">
						<span class="start" v-show='!bautionerrs'></span><i v-show='!bautionerrs' class="starts">从</i>
						<el-date-picker v-show='!bautionerrs' class='ipt' type="date" v-model='date1'></el-date-picker>
						<span class="start1" v-show='bautionoks'></span><i v-show='bautionoks' class="starts2">至</i>
						<el-date-picker v-show='!bautionerrs' class='ipt' type="date" v-model='date2'></el-date-picker>
						<span v-show='bautionerrs' class="domain" v-for='(item,index) in dat'>{{item}}<i v-if="index < dat.length - 1">&nbsp;&nbsp;&nbsp;&nbsp;至 </i> </span> <span @click="showipt" v-show='shows' :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
					</el-form-item>
					<el-form-item label="营业执照扫描件">
						<div class="picInfo">
							<div class="busPic left" v-if='buspic.length==0'>
								<img src="../../img/busspic.png" alt="" />
							</div>
							<div class="busPic left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+buspic[0]" alt="" />
								<big-img v-if="showbus" @clickit="viewbus" :imgSrc="busPicsrc"></big-img>
								<i class="el-icon-view" @click='clickbus'></i>
							</div>
							<img-upload @send-image='getBuspic' :ImageSize='imgSize'></img-upload>
							<span @click="showipt" v-show='bautionerrs' class="busp" :class="[{gr:bautionoks},{reds:bautionerrs},{or:binautions}]">({{bscert}})</span>
							<div class="text-warp">
								<p class="text top pstion">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form :inline="true">
						<el-form-item class='citys' label='餐饮服务许可证编号'>
							<el-select v-model="value" placeholder="省份(简称)" v-show='!rautionoks'>
								<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
								</el-option>
							</el-select>
							<span class="domain" v-show='rautionoks'>{{shopsbusiness.repast_permit_identity}}餐证字 ({{shopsbusiness.repast_permit_year}}) 第{{shopsbusiness.repast_permit_num}}号 <span v-show='rautionoks' :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span></span>
						</el-form-item>
						<el-form-item class='citys cit' label='餐证字' v-show='!rautionoks'>
							<el-date-picker v-model="value5" align="right" type="year" placeholder="发证年份">
							</el-date-picker>
						</el-form-item>
						<el-form-item class='cityd' label='第' v-show='!rautionoks'>
							<el-input placeholder='输入行政区域代码及发证顺序编号' v-model='forms.repast_permit_num'></el-input>
						</el-form-item>
						<el-form-item label='号' v-show='!rautionoks'>

						</el-form-item>
					</el-form>

					<el-form-item label="餐饮服务许可证扫描件">
						<div class="picInfo">
							<div class="res left" v-if="respic.length==0">
								<img src="../../img/respic.png" alt="" />
							</div>
							<div class="res left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+respic[0]" alt="" />
								<big-img v-if="showres" @clickit="viewres" :imgSrc="resPicsrc"></big-img>
								<span @click="showipt" v-show='rautionoks' class="resp" :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
								<i class="el-icon-view" @click='clickres'></i>
							</div>
							<img-upload @send-image='getRespic' :ImageSize='imgSize'></img-upload>
							<div class="text-warp">
								<p class="text top pstion">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="确认书扫描件">
						<div class="picInfo">
							<div class="confim left" v-if="confm.length==0">
								<img src="../../img/confim.png" alt="" />
							</div>
							<div class="confim left" v-else>
								<div class="bg"></div>
								<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+confm[0]" alt="" />
								<big-img v-if="showcos" @clickit="viewcos" :imgSrc="cosPicsrc"></big-img>
								<span @click="showipt" v-show='rautionoks' class="resp" :class="[{gr:rautionoks},{reds:rautionerrs},{or:rinautions}]">({{rscert}})</span>
								<i class="el-icon-view" @click='clickcos'></i>
							</div>
							<img-upload @send-image='getConfimpic' :ImageSize='imgSize'></img-upload>
							<div class="downLoad">
								<p class="load">下载确认书模板</p>
								<p class="text">注：支持JPG、JPEG、PNG文件格式，大小不得大于2M</p>
							</div>
						</div>
					</el-form-item>
					<el-form-item label="经营范围">
						<el-input v-model='forms.business_scope' v-show='!rautionoks'></el-input>
						<span class="domain" v-show='rautionoks'>{{shopsbusiness.business_scope}}</span>
					</el-form-item>
					<div class="bottom">
						<button type='button' class="blue" @click='onSubmit'>提交审核</button>
						<button type="button" class="default" @click='goBack'>关闭</button>
					</div>
				</el-form>
			</div>
		</section>
	</div>
</template>

<script>
	import { saveShopinfo,CommerStatus } from '../../../api';
	import { getShopinfo } from '../../../api';
	import { Util } from '@/config/util';
	import { Business,Idcode,Dining  } from '@/config/cfg';
	import ImgUpload from '@/components/upload/SUpload';
	import { Confimcard , Character,Busnumber,Resnumber } from '../../../index';
	import BigImg from '../../../components/showBigImg';
	import { Public } from '@/config/public';
	export default {
		components:{
			ImgUpload,
			BigImg
		},
		data() {
			return {
				options: [{   //省份简称
						value: '京'
					}, {
						value: '津'
					}, {
						value: '渝'
					}, {
						value: '沪'
					}, {
						value: '冀'
					}, {
						value: '晋'
					},
					{
						value: '辽'
					},
					{
						value: '吉'
					}, 

					{	value: '黑'
					}, {
						value: '苏'
					}, {
						value: '浙'
					}, {
						value: '皖'
					}, {
						value: '闽'
					}, {
						value: '赣'
					}, {
						value: '鲁'
					}, {
						value: '豫'
					}, {
						value: '鄂'
					}, {
						value: '湘'
					}, {
						value: '粤'
					}, {
						value: '琼'
					}, {
						value: '川'
					}, {
						value: '贵'
					}, {
						value: '云'
					}, {
						value: '陕'
					}, {
						value: '甘'
					}, {
						value: '青'
					}, {
						value: '内蒙古'
					}, {
						value: '桂'
					}, {
						value: '宁'
					}, {
						value: '新'
					}, {
						value: '藏'
					}, {
						value: '港'
					}, {
						value: '澳'
					}
				],
				value: '', //存选中省的简称
				date1: '', //开始日期
				date2: '', //结束日期
				value5: '', //保存选中的年份
				forms: {
					company_name: '',   //企业名称
					legal_person: '',   //法人代表
					legal_card: '',   //法人身份证
					business_num: '',//  营业执照
					repast_permit_identity: '', //营业期限
					repast_permit_year: '', //发证年份
					repast_permit_num: '', // 证号
					business_scope: '' //经营范围
				},
				shopsbusiness: {},   //用来接收获取过来的数据
				dates: [],  //保存营业期限日期
				years: '', // 发证年份
				imgbase_url: imgbase_url, //图片地址
				show: true, //控制输入框的显示与隐藏
				shows:true, //控制输入框的显示与隐藏
				dat: [],  //获取营业期限日期
				cert:{},  //获取认证状态
				bscert:'', //营业部分状态
				idcert:'', //身份部分状态
				rscert:'', //餐饮部分状态
				inaution:'or', //添加class类
				autionerr:'reds', //添加class类
				autionok:'gr', //添加class类
/*控制三大部分状态样式*/
				rinautions:false,
				rautionoks:false,
				rautionerrs:false,
				binautions:false,
				bautionoks:false,
				bautionerrs:false,
				Iinautions:false,
				Iautionoks:false,
				Iautionerrs:false,
/*控制三大部分状态样式*/
				legal_card_photo:[],//保存身份证
				imgbase_url: imgbase_url , //默认图片地址
				buspic:[], //保存营业执照图片
				respic:[], //保存餐饮许可证图片
				confm:[], //保存餐饮许可证确认书
				imgSize:2048, //上传图片尺寸上限
				imgSrc:'',
				showImg:false,
				showimg:false,
				showbus:false,
				showres:false,
				showcos:false,
				Imgsrc:'',
				busPicsrc:'',
				resPicsrc:'',
				cosPicsrc:''
			};
		},
		created() {
			this.getData();
			this.getStatus();
		},
		computed:{
			Business(){
				return Business;
			},
			Idcode(){
				return Idcode;
			},
			Dining(){
				return Dining;
			}
		},
		methods: {
	        getLegarcard(res){     	
	        	this.legal_card_photo.push(res);
	        	if(this.legal_card_photo.length>2){
	        		this.legal_card_photo = [];
	        	}
	        },
	        getBuspic(res){
	        	this.buspic = [];
	        	this.buspic.push(res);
	        },
	        getRespic(res){
	        	this.respic = [];
	        	this.respic.push(res);
	        },
	        getConfimpic(res){
	        	this.confm = [];
	        	this.confm.push(res);
	        },
	         clickImg() {
                this.showImg = true;
                // 获取当前图片地址
                this.imgSrc = this.legal_card_photo[0];
           },
            viewImg(){
                this.showImg = false;
            },
	        clickimg(){
	        	 this.showimg = true;
                // 获取当前图片地址
                this.Imgsrc = this.legal_card_photo[1];
	        },
	        viewimg(){
	        	this.showimg = false;
	        },
	        clickbus(){
	        	this.showbus = true;
	        	this.busPicsrc = this.buspic[0];
	        },
	        viewbus(){
	        	this.showbus = false;
	        },
	         clickres(){
	        	this.showres = true;
	        	this.resPicsrc = this.respic[0];
	        },
	        viewres(){
	        	this.showres = false;
	        },
	         clickcos(){
	        	this.showcos = true;
	        	this.cosPicsrc = this.confm[0];
	        },
	        viewcos(){
	        	this.showcos = false;
	        },
			
/*保存数据*/
			onSubmit() {
					if(this.isEmpty(this.forms)){
						alert('所有信息必须全部填写！！')
						return ;
					}
					if(!Character(this.forms.company_name)){
						alert('企业名称只能是中文!!')
						return ;
					}
					if(this.forms.company_name==''){
						alert('企业名称不能为空！！')
						return;
					}
					if(this.forms.legal_person==''){
						alert('法人代表不能为空')
						return;
					}
					if(this.forms.legal_card==''){
						alert('身份证号码必须填写！！')
						return;
					}
					
					if(!Confimcard(this.forms.legal_card)){
						alert('请输入合法的身份证信息！！！')
						return ;
					}
					if(!Character(this.forms.business_scope)){
						alert('餐饮范围只能是中文')
						return ;
					}
					if(!Busnumber(this.forms.business_num)){
						alert('请输入正确的营业执照注册号！！')
						return ;
					}
					if(!Resnumber(this.forms.repast_permit_num)){
						alert('许可证号最多只能输入12位数字');
						return ;
					}
				
				let t = new Date(this.date1);
				let startTime = t.getFullYear() + '-' + (t.getMonth() + 1) + '-' + t.getDate();
				let statime = startTime;
				let sta1 = Date.parse(new Date(statime));
				sta1 = sta1 / 1000;
				let t2 = new Date(this.date2);
				let endTime = t2.getFullYear() + '-' + (t2.getMonth() + 1) + '-' + t2.getDate();
				let entime = endTime;
				let sta2 = Date.parse(new Date(entime));
				sta2 = sta2 / 1000;
				this.dates.push(sta1, sta2);
				let t3 = new Date(this.value5);
				this.years = t3.getFullYear();
				let shop_id = Public.getShopid();
				let data = {
					save_shop_business: 1,
					shop_id:shop_id,
					company_name: this.forms.company_name,
					legal_person: this.forms.legal_person,
					legal_card: this.forms.legal_card,
					business_num: this.forms.business_num,
					repast_permit_identity: this.value,
					repast_permit_year: this.years,
					legal_card_photo:this.legal_card_photo,
					business_photo:this.buspic[0],
					repast_permit_photo:this.respic[0],
					confirmation:this.confm[0],
					repast_permit_num: this.forms.repast_permit_num,
					business_scope: this.forms.business_scope,
					business_date: this.dates
				};
				saveShopinfo(data, this.alts);
			},
				alts(resp) {
					if(resp.ret === 0) {
						
						this.$router.go(-1);
						
					} else {
						alert('保存失败');
					}
			},
/*保存数据*/
			goBack() {   //跳转到店铺信息
				this.$router.go(-1);
			},
			
			isEmpty(obj){
				for(let key in obj){
					if(!obj[key]){
						return;
					}
				}
			},
			
			showipt(){
				
				this.bautionerrs = false;
				this.show = false;
				this.shows = false;
			},
			
/*获取数据*/
			getData() {
				let _this = this;
				getShopinfo({
					get_shop_business: 1,
				}, function(resp) {
					if(resp){
						_this.shopsbusiness = resp.data.shopsbusiness||{};
						_this.forms.company_name = _this.shopsbusiness.company_name;
						_this.forms.legal_person = _this.shopsbusiness.legal_person;
						_this.forms.legal_phone = _this.shopsbusiness.legal_phone;
						_this.forms.legal_card = _this.shopsbusiness.legal_card;
						_this.forms.business_num = _this.shopsbusiness.business_num;
						_this.forms.business_scope = _this.shopsbusiness.business_scope;
						_this.value5 = new Date(String(_this.shopsbusiness.repast_permit_year));
						_this.value = _this.shopsbusiness.repast_permit_identity;
						_this.forms.repast_permit_num = _this.shopsbusiness.repast_permit_num;
						_this.legal_card_photo = _this.shopsbusiness.legal_card_photo || [];
						if(!Util.isEmpty(_this.shopsbusiness.business_date)){
							let time1 = _this.shopsbusiness.business_date.slice(1, length - 1);
							let arr = time1.split(',');
							let time2 = arr[0];
							let stime = new Date();
							stime.setTime(time2 * 1000);
							_this.date1 = stime.toString();
							let time3 = arr[1];
							let etime = new Date();
							etime.setTime(time3 * 1000);
							_this.date2 = etime.toString();
							let time4 = _this.shopsbusiness.business_date.slice(1, length - 1);
							let arr3 = time1.split(',');
							let time5 = arr[0];
							let time6 = arr[1];
							let s1time = Util.TimeTo(time5, 'yyyy-MM-dd');
							let e1time = Util.TimeTo(time6, 'yyyy-MM-dd');
							_this.dat.push(s1time, e1time);
						}
						if(!Util.isEmpty(_this.shopsbusiness.business_photo)){
							_this.buspic.push(_this.shopsbusiness.business_photo);
						}
						if(!Util.isEmpty(_this.shopsbusiness.repast_permit_photo)){
							_this.respic.push(_this.shopsbusiness.repast_permit_photo);
						}
						if(!Util.isEmpty(_this.shopsbusiness.confirmation)){
							_this.confm.push(_this.shopsbusiness.confirmation);
						}
						
					}
				});
			},
/*获取数据*/
			getStatus(){
				let _this =this;
				CommerStatus({
					get_shop_bs_status: 1,
				},function(resp){
					if(resp){
						_this.cert = resp.data.shop_bs_status||{};
					if(_this.cert.bs_code===Business.AUTONERR){
						_this.bscert = Business.toString(Business.AUTONERR);
						_this.bautionerrs  = true;
					}else if(_this.cert.bs_code===Business.NO){
						_this.bscert = Business.toString(Business.NO);
					}else if(_this.cert.bs_code===Business.INAUTON){
						_this.bscert = Business.toString(Business.INAUTON);
					}else if(_this.cert.bs_code===Business.YES){
						_this.bscert = Business.toString(Business.YES);
					}
					if(_this.cert.id_code===Idcode.AUTONERR){
						_this.idcert = Idcode.toString(Idcode.AUTONERR);
					}else if(_this.cert.id_code===Idcode.NO){
						_this.idcert = Idcode.toString(Idcode.NO);
						_this.Iautionerrs = true;
//						_this.forms.company_name = '';
//						_this.forms.legal_person = '';
//						_this.forms.legal_card = '';
					}else if(_this.cert.id_code===Idcode.INAUTON){
						_this.idcert = Idcode.toString(Idcode.INAUTON);
						_this.Iinautions = true;
					}else if(_this.cert.id_code===Idcode.YES){
						_this.idcert = Idcode.toString(Idcode.YES);
					}
					if(_this.cert.rs_code===Dining.AUTONERR){
						_this.rscert = Dining.toString(Dining.AUTONERR);
					}else if(_this.cert.rs_code===Dining.NO){
						_this.rscert = Dining.toString(Dining.NO);
					}else if(_this.cert.rs_code===Dining.INAUTON){
						_this.rscert = Dining.toString(Dining.INAUTON);
					}else if(_this.cert.rs_code===Dining.YES){
						_this.rscert = Dining.toString(Dining.YES);
						_this.rautionoks = true ;
					}
					}
					
				});
			}
		}
	};
</script>

